import Vue from 'vue'
import App from './App'
import router from './router'
import { Button, Select, Row, Input, Form, FormItem, Table, TableColumn, Dialog, DatePicker } from 'element-ui'

Vue.use(Button)
Vue.use(Select)
Vue.use(Row)
Vue.use(Input)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Dialog)
Vue.use(DatePicker)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



