<template>
  <el-form class="login-form" label-width="80px" ref="loginForm" :model="loginForm" :rules="loginRules">
    <h1 class="login-form__title">账号登录</h1>
    <el-form-item label="用户名" prop="account">
      <el-input v-model="loginForm.account" @keyup.native.enter="handleLogin"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="password">
      <el-input type="password" v-model="loginForm.password" @keyup.native.enter="handleLogin"></el-input>
    </el-form-item>
    <el-row>
      <el-button type="primary" @click="handleLogin">登录</el-button>
      <el-button @click="goRegistry">注册</el-button>
    </el-row>
  </el-form>
</template>

<script>
import { request } from '@/assets/js/util'
import { Message } from 'element-ui'

export default {
  name: 'LoginForm',
  data () {
    // 注册时验证账号
    var accountValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入账号'))
      } else {
        callback()
      }
    }
    // 注册时验证密码
    var passValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }

    return {
      loginForm: {
        account: '',
        password: ''
      },
      loginRules: {
        account: [
          { validator: accountValidate, trigger: 'blur' }
        ],
        password: [
          { validator: passValidate, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    /**
     * 登录处理
     */
    handleLogin () {
      const { loginForm } = this.$refs
      const { account, password } = this.loginForm

      loginForm.validate(valid => {
        if (valid) {
          request({
            method: 'post',
            url: '/api/login',
            data: {
              account,
              password
            }
          }).then(res => {
            if (res.status === 1000 && res.success) {
              this.$router.push({
                name: 'index'
              })
            } else {
              loginForm.clearValidate()
              Message({
                showClose: true,
                message: res.msg,
                type: 'error'
              })
            }
          })
        } else {
          return false
        }
      })
    },
    goRegistry () {
      this.$router.replace({
        name: 'loginRegistry',
        params: {
          state: 'registry'
        }
      })
    }
  }
}
</script>



